* {
    box-sizing: border-box;
  }
  
body {
margin: 0;
padding: 0;
background-color: #141414;
color:#fff;
height: 100%;
}

html,
body,
#potato,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu_bar {background:cornsilk;position: fixed; left:0; top:0;}
.menu_bar a {color:red; display:block; padding: 40px 30px;text-decoration: none;}
.menu_bar a:first-child {font-weight: 700;}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 100%;
}

.movies .movie {
    width: 25%;
    height: 740px;
    background-color: #282828;
    margin-bottom: 70px;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #000;
    text-align: center;
    border: 5px solid red;
}

.movies .movie a {
    text-decoration: none;
  }


.movies .movie .movie_data {color:#fff}
.movies .movie .movie_data .genres {list-style: none;padding:0;}
.movies .movie .movie_data .genres li {display:inline; padding: 0 5px; color:red;}